	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/posts/articulation-exercises/index_ru.md absPath of file >>> MarkdownRemark","html":"<p>Этим упражнениям меня учили на моих первых курсах английского языка. Выглядит\nсмешно, но оказыватся полезным.<excerpt/></p>\n<h3>Для губ</h3>\n<ol>\n<li>Вытянуть губы в трубочку, растянуть улыбку со сведенными губами.</li>\n<li>То же самое с открытыми губами.</li>\n<li>Вытянуть «хоботок» и делать круговые движения.\n1.Петь слитно: и, э, а, о, у, ы.</li>\n<li>Произносить быстро:<br/>\nпи, пи, пи,<br/>\nпэ, пэ, пэ,<br/>\nпа, па, па,<br/>\nпо, по, по,<br/>\nпу, пу, пу,<br/>\nпы, пы, пы.</li>\n<li>Попеременно: вытянуть губы, раскрыть рот.</li>\n</ol>\n<h3>Для челюсти</h3>\n<ol>\n<li>Нижнюю челюсть в одну сторону, потом в другую. Верхняя челюсть неподвижна.</li>\n<li>Открывать и закрывать рот, двигая нижнюю челють вниз.</li>\n</ol>\n<h3>Для языка</h3>\n<p>Эти упражнения лучше делать с зеркалом. Может понадобиться платок, чтобы\nвытирать рот.</p>\n<ol>\n<li>Вытянуть язык, сделать острым, потом растянуть.</li>\n<li>Язык выглянул, напрягся, расслабился, спрятался.</li>\n<li>Язык выглянул, направо, налево, прямо, спрятался.</li>\n<li>Делать языком «часики»: направо, налево.</li>\n<li>С закрытым ротом упираться языком в щёку и давить на неё.</li>\n<li>Круговые движения с высунутым языком.</li>\n<li>Рот открыт, язык напряженный. Доставать и убирать язык.</li>\n<li>Кончиком языка гладить нёбо вперёд, назад.</li>\n<li>Высунуть язык, делать движения вверх, вниз кончиком языка.</li>\n<li>Упражнение «грибок»: плоский язык прижимать к нёбу.</li>\n<li>Пощёлкать языком.</li>\n<li>Сложить язык трубочкой.</li>\n</ol>","fields":{"slug":"ru/posts/articulation-exercises/","prefix":"","disqusIdentifier":"ru-posts-articulation-exercises"},"frontmatter":{"title":"Упражнения для дикции","date":"01 June 2013","v2":null,"old":true,"tumblr":null,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/posts/articulation-exercises/","prev":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/posts/beml-html-preprocessor/index_en.md absPath of file >>> MarkdownRemark","fields":{"slug":"en/posts/beml-html-preprocessor/","prefix":"","lang":"en","disqusIdentifier":"en-posts-beml-html-preprocessor"},"frontmatter":{"title":"BEML - an HTML preprocessor for BEM"}}}}

